.app-table-container {
  height: 100%;
  width: 100%;
  box-shadow: #0000007c 1px 1px 4px;
  border-radius: 5px;
}

.app-table-container table {
  width: 100%;
  border-collapse: collapse;
}

.app-table-container .table-header {
  width: 100%;
  background-color: #fff;
  color: rgb(218 105 105);
  position: relative;
  overflow-x: scroll;
  overflow-y: hidden;
  box-shadow: #0000007c 1px 1px 4px;
  height: 60px;
  scrollbar-width: none;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.app-table-container .table-header table {
  margin-right: 10px;
  height: 100%;
}

.app-table-container .table-header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  bottom: -3px;
  left: 0;
  box-shadow: #0000007c 0px 1px 8px;
  z-index: -1;
}

.app-table-container .table-body {
  overflow: auto;

  overflow: scroll;
  height: calc(100% - 50px);
}

.app-table-container .table-body table tbody tr:nth-child(even) {
  background-color: #dfdfdf !important;
}

.app-table-container .table-header table thead tr th,
.app-table-container .table-body table tbody tr td {
  text-align: left;
  min-width: 200px;
  background-color: #fff;
  padding-left: 10px;
}

.app-table-container .table-header table thead tr th:not(:first-child) {
  cursor: pointer;
  transition: padding 0.2s ease-in-out;
}

.app-table-container .table-header table thead tr th:not(:first-child):hover {
  padding-left: 20px;
  background-color: rgba(196, 196, 196, 0.767) !important;
}

.app-table-container .table-header table thead tr th:not(:first-child) > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  height: 100%;
}

.app-table-container .table-body table tbody tr td:first-child,
.app-table-container .table-body table tbody td:not(:first-child) {
  transition: background 0.2s ease-in-out, color 0.2s ease-in-out,
    padding 0.4s ease-in-out;
}

.app-table-container .table-body table tbody tr:hover td:first-child,
.app-table-container .table-body table tbody td:hover:not(:first-child) {
  background-color: #ad7979 !important;
  color: #fff;
  padding-left: 15px;
}

.app-table-container .table-header table thead tr {
  height: 100%;
}
.app-table-container .table-header table thead tr th:first-child {
  height: 100%;
  display: flex !important;
  align-items: center;
}

.app-table-container .table-header table thead tr th:first-child,
.app-table-container .table-body table tbody tr td:first-child {
  position: relative;
  position: sticky;
  z-index: 100;
  left: 0;
}

.app-table-container .table-header table thead tr th:first-child::after,
.app-table-container .table-body table tbody tr td:first-child::after {
  position: absolute;
  top: -10px;
  right: 0;
  bottom: 0;
  width: 30px;
  transform: translateX(100%);
  transition: box-shadow 0.5s;
  content: "";
  pointer-events: none;
  box-shadow: inset 10px 0 8px -8px rgba(5, 5, 5, 0.06);
}

.app-table-container .table-body table tbody tr td {
  border-bottom: 1px solid #f0f0f0;
  color: rgb(82, 81, 81);
  font-family: "Roboto";
  font-weight: 500;
  height: 60px;
}

/* Scroll Bar */

.app-table-container .table-body::-webkit-scrollbar {
  width: 10px !important;
}

.app-table-container .table-body::-webkit-scrollbar-thumb {
  background-color: #888 !important;
  border-radius: 10px !important;
}

.app-table-container .table-body::-webkit-scrollbar-thumb:hover {
  background-color: #555 !important;
}

.app-table-container .table-body::-webkit-scrollbar-track {
  background-color: #eee !important;
  border-radius: 10px !important;
}

/* Customize the scrollbar for Firefox */
.app-table-container .table-body {
  scrollbar-width: thin !important;
  scrollbar-color: #888 #eee !important;
}

.app-table-container .table-body:hover {
  scrollbar-color: #555 #eee !important;
}

.app-table-container .table-body::-moz-scrollbar-thumb {
  border-radius: 10px !important;
}

.app-table-container .table-body::-moz-scrollbar-track {
  border-radius: 10px !important;
}

.app-table-container .table-header::-webkit-scrollbar {
  display: none;
}

.list {
  width: 100%;
  background: #ffffff;
  height: 300px;
  overflow: scroll;
}

.list ul {
  padding: 10px;
  display: grid;
}

.list ul li {
  list-style: none;
  padding: 15px 10px;
  margin-top: 10px;
  border: 1px solid grey;
  width: 100%;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #555;
  border: 1px solid #c6cbd4;
  border-radius: 5px;
  background: none;
}
.list ul li:hover {
  background-color: #f5efef9f;
}

.list ul li.selected {
  border: 1px solid #d33838;
}
.list ul li.disabled {
  border: 1px solid #c6cbd4;
  opacity: 0.4;
  cursor: default;
}

.list ul li button {
  background: none;
  width: 100%;
  height: 100%;
}
