.page-byfilters {
  padding: 0 20px;
}

.page-byfilters .tools-box-container {
  padding: 5px 7px;
  margin: 10px 0;
  background-color: #fff;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 5px;
}

.page-byfilters section {
  padding: 10px 0;
  height: 100%;
}

.page-byfilters .tools-box-container.statistic {
  padding: 5px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px, 0 8px 0 0 grey;
}

