.page-byfilters .tools-box-container.statistic .control-buttons {
  display: flex;
  align-items: center;
}

.page-byfilters .tools-box-container.statistic .control-buttons .clear-btn {
  height: 36px;
  width: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin-left: 15px;
}
