.table-filter-items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  background-color: #fff;
  box-shadow: rgba(247, 204, 204, 0.16) 0px 1px 4px;
  padding: 10px;
  row-gap: 10px;
  column-gap: 40px;
  padding-bottom: 10px;
}

.table-filter-item {
  display: flex;
  align-items: center;
}

.table-filter-container .clear-btn {
  height: 36px;
  width: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin-right: 15px;
  margin-bottom: 15px;
  float: right;
}
