.compare-page {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
  z-index: 1;
}
.compare-page .form {
  width: 40%;

  padding: 50px 15px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  display: flex;
  flex-direction: column;
}

.compare-page .form label {
  display: flex;
  flex-direction: column;
  margin: 8px 0;
}

.compare-page .form label > span {
  margin-bottom: 5px;
}

.compare-page .form .btn-submit {
  margin-top: 10px;
}

.compare-page .compare-container {
  width: 80%;
  height: 80%;
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  margin-top: 30px;
}

.compare-page .compare-container .compare-card {
  width: 40%;
  height: 80%;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  transition: transform 0.2s ease;
  font-family: Arial, Helvetica, sans-serif;
  color: #fff;
  position: relative;
  color: #000;
}

.compare-page .compare-container .compare-card .header {
  padding: 20px;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
}

.compare-page .compare-container .compare-card .header .title {
  width: 100%;

  font-size: 22px;
  font-weight: 500;
}

.compare-page .compare-container .compare-card .description {
  padding: 10px;
  line-height: 25px;
}

.compare-page .compare-container .compare-card .compare-btn {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.compare-page .compare-container .compare-card:hover {
  transform: scale(1.025);
}

.compare-page .categories-container {
  width: calc(80% - 48px);
  height: 80%;
  background-color: #fff;
  align-self: flex-start;
  margin-top: 50px;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.categories-container > .header {
  text-align: center;
  margin: 15px 0;
  font-family: Arial, Helvetica, sans-serif;
}

.categories-container .header .title {
  color: rgba(0, 0, 0, 0.77);
  font-size: 22px;
}
.categories-container .header .subtitle {
  color: rgba(143, 143, 143, 0.77);
  font-size: 15px;
  margin-top: 5px;
}

.categories-container .categories-list-items {
  padding: 0 10px;
}

.categories-container .categories-list-items {
  display: flex;
  flex-direction: row;

  flex-wrap: wrap;
  height: calc(100% - 83px);
  overflow: scroll;
  align-content: flex-start;
}
.categories-container .categories-list-items .categories-list-item {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  margin: 10px;
  background-color: #fff;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  padding: 10px;
  border-radius: 10px;
  width: 120px;
  height: 180px;
  transition: transform 0.4s ease;
  cursor: pointer;
  position: relative;
}

.categories-container .categories-list-items .categories-list-item:hover {
  transform: scale(1.05);
}

.categories-container .categories-list-items .categories-list-item:hover {
  transform: scale(1.05);
}

.categories-container
  .categories-list-items
  .categories-list-item
  div.categorie-img {
  width: 80px;
  height: 80px;
  position: relative;
  padding: 10px;
  box-sizing: content-box;
  border-radius: 50%;

  box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset,
    rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
}
.categories-container
  .categories-list-items
  .categories-list-item
  div.categorie-img
  img {
  width: 100%;
  height: 100%;
  transition: border 1s ease;

  border-radius: 50%;
}

.categories-container
  .categories-list-items
  .categories-list-item
  div.categorie-img::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border: 2px dashed grey;
  border-radius: 50%;
  box-sizing: content-box;
  transform: scale(0.1);
  transition: transform 0.4s ease-in-out;
}

.categories-container
  .categories-list-items
  .categories-list-item:hover
  div.categorie-img::after {
  transform: scale(1) rotate(360deg);
  border-color: #1677ff;
}

.categories-container
  .categories-list-items
  .categories-list-item:hover
  img.categorie-img {
  border-color: #1677ff;
}

.categories-container
  .categories-list-items
  .categories-list-item
  span.categorie-name {
  max-height: 3em;
  max-width: 100px; /* Set a maximum height for two lines */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* Number of lines to show */
  -webkit-box-orient: vertical;
  white-space: normal;
  margin-top: 7px;
  font-size: 14px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.77);
  text-transform: capitalize;
}

.categories-container
  .categories-list-items
  .categories-list-item
  .categorie-count {
  background-color: rgb(250, 63, 63);
  color: #ffffff;
  font-weight: 500;
  position: absolute;
  top: 3px;
  right: 3px;
  font-size: 12px;
  padding: 3px;
  border-radius: 10px;
  z-index: 100;
}
.categories-list-items .empty {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 768px) {
  .compare-page {
    align-items: baseline;
    padding-top: 30px;
  }
  .compare-page .form {
    width: 94%;
  }
}
