.image-picker-container {
  margin: 5px;
  width: fit-content;
}
.image-picker-placeholder {
  position: relative;
  background-color: rgb(255, 255, 255);
  border: 1px dashed #ddd;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.image-picker-placeholder:hover {
  border: 1px dashed #1677ff;
}
.image-picker-placeholder .image-picker-input {
  cursor: pointer;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.image-picker-placeholder-img {
  width: 100%;
  height: 100%;
}
