.app-.app-table table {
  width: 100%;
  overflow: scroll;

  background: #fafafa;
  user-select: none;
}

.table-container {
  overflow: scroll;
  display: flex;
  max-height: calc(100vh - 300px);
}

.app-table table {
  width: 100%;
  border-collapse: collapse;
  color: #5c5c5c;
  text-align: center;
  max-height: calc(100vh - 200px);
}

.app-table tabletbody tr:nth-child(even) {
  background-color: #f3f4f9;
}

.app-table th {
  position: sticky;
  top: -1px;
  z-index: 1; /* Add a z-index to ensure the header is above the content */
}

.app-table th,
.app-table td {
  border-bottom: 1px solid #ebeaea;
  height: 10px !important;
  text-wrap: nowrap;
  border-collapse: separate;
  padding-top: 15px;
  padding-bottom: 15px;
  font-weight: 600;
  color: #000;
}

/* Add the following styles to remove unnecessary borders on the first row */
.app-table table thead tr th:first-child {
  border-top: none;
  border-left: none;
  position: sticky;
  top: -1px;
  left: -1px;
  background-color: #ffffff;
  z-index: 70;

  box-sizing: content-box;
}

.app-table table tbody tr:hover td:first-child {
  box-shadow: #00000060 1px 0px 8px;
}

.app-table table tbody tr:hover td:first-child {
  background-color: #f0f0f0 !important;
  color: #fff;
}

.app-table table thead tr th:not(:first-child) {
  color: #fff;
  font-size: 18px;
}

.app-table table tbody tr td:hover {
  background: rgb(241, 239, 239);
}

.app-table table tbody tr td:first-child {
  position: sticky;
  left: 0;
  z-index: 60;
  color: #fff;
  width: 350px;
}

.app-table table tbody tr td:not(:first-child) {
  color: #283664;
  font-weight: 600;
  font-size: 18px;
}

.app-table table tbody tr td:first-child::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.table-container {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px !important;
}

.app-table thead {
  box-shadow: #0000002f 0px 4px 20px;
}

.app-table thead th,
.app-table tbody td {
  padding: 0 !important;
  margin: 0 !important;
  padding: 12px 0 !important;
}

.app-table thead th {
  border: none !important;
  color: rgb(243, 82, 82) !important;
  padding: 15px 0 !important;
}

.app-table tbody td {
  color: rgb(124, 124, 124) !important;
  font-size: 16px;
  font-weight: 400 !important;
}
